.trip {
    margin: 4rem 6rem;
    color: #2a2a2a;
}

.tripCards {
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.t-cards {
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
}

.t-image {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}


.t-image img {
    transition: 0.3s ease-in-out;
    width: 100%;
    height: 100%;
    border-radius: 7px;
}

.t-image:hover img {
    transform: scale(1.3);
}

.t-cards h4 {
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}


@media screen and (max-width: 850px) {
    .trip{
        margin: 4rem 2rem;
    }

    .tripCards {
        flex-direction: column;
    }

    .t-cards{
        width: 100%;
        margin-bottom: 1.5rem;
    }
    
}